/* eslint-disable no-extend-native */
import React, { useEffect, useState } from "react";

export default function AwanaTable() {
  let [NewLeaders, SetNewLeaders] = useState({
    Games: {
      One: [],
      Two: [],
    },
    Games2: {
      One: [],
      Two: [],
    },
    Hymns: [],
    Prayers: [],
    Verses: [],
    Lesson: [],
    Dates: [],
  });

  useEffect(() => {
    let Leaders = [
      "سوسنه عماد",
      "سوسنه موريس",
      "رامي عماد",
      "مندي شفيق",
      "مندي عادل",
      "فاديا فرحان",
      "مريم نادي",
      "هنا مكرم",
      "نيفين مرزوق",
      "مريان مجدي",
      "مرتينا اشرف",
      "انجي نادي",
      "اميرة نسيم",
      "ميلاد رضا",
    ];
    let Leaders1 = [
      "سوسنه عماد",
      "سوسنه موريس",
      "رامي عماد",
      "مندي شفيق",
      "مندي عادل",
      "فاديا فرحان",
      "مريم نادي",
      "هنا مكرم",
      "نيفين مرزوق",
      "مريان مجدي",
      "مرتينا اشرف",
      "انجي نادي",
      "اميرة نسيم",
      "ميلاد رضا",
    ];
    NewLeaders.Lesson = Leaders;
    Leaders = Move(0, Leaders.length - 1, Leaders);
    NewLeaders.Verses = Leaders;
    Leaders = Move(0, Leaders.length - 1, Leaders);
    NewLeaders.Hymns = Leaders;
    Leaders = Move(0, Leaders.length - 1, Leaders);
    NewLeaders.Prayers = Leaders;

    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    NewLeaders.Games.One = Leaders1;
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    NewLeaders.Games.Two = Leaders1;

    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    NewLeaders.Games2.One = Leaders1;
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    Leaders1 = Move(0, Leaders.length - 1, Leaders1);
    NewLeaders.Games2.Two = Leaders1;

    Date.prototype.addDays = function (days) {
      var date = new Date(this.valueOf());
      date.setDate(date.getDate() + days);
      return date;
    };
    let StartDay = new Date("2023-11-24");
    let Dates = [];
    for (let i = 0; i < Leaders1.length; i++) {
      let NewDate = new Date(StartDay.addDays(i * 7));
      Dates.push(NewDate);
    }
    NewLeaders.Dates = Dates;
    SetNewLeaders(NewLeaders);
    console.log(NewLeaders);
  }, []);
  const Move = (From, To, Array) => {
    let array = [...Array];
    const element = array.splice(From, 1)[0];

    array.splice(To, 0, element);

    return array;
  };
  function formatDate(date) {
    return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
  }
  return (
    <section id="AwanaTable" className="">
      <table className="table table-dark" dir="rtl">
        <thead>
          <tr>
            <th>م</th>
            <th>التاريخ</th>
            <th>فوق العاب</th>
            <th>تحت العاب</th>
            <th>صلاة وقيادة</th>
            <th>ترانيم</th>
            <th>ايه</th>
            <th>درس</th>
          </tr>
        </thead>
        <tbody>
          {NewLeaders.Verses.map((Verse, i) => {
            return (
              <tr key={i}>
                <th>{i + 1}</th>
                <th>{formatDate(NewLeaders.Dates[i])}</th>
                <th>
                  {NewLeaders.Games.One[i]}, {NewLeaders.Games.Two[i]}
                </th>
                <th>
                  {NewLeaders.Games2.One[i]}, {NewLeaders.Games2.Two[i]}
                </th>
                <th>{NewLeaders.Prayers[i]}</th>
                <th>{NewLeaders.Hymns[i]}</th>
                <th>{NewLeaders.Verses[i]}</th>
                <th>{NewLeaders.Lesson[i]}</th>
              </tr>
            );
          })}
        </tbody>
      </table>
      <br />
      <hr />
      <br />
      <div className="Days">
        {NewLeaders.Verses.map((Verse, i) => {
          return (
            <div className="Day bg-primary" key={i}>
              <span className="M">{i + 1}</span>
              <p>
                <span>العاب فوق :</span>
                {NewLeaders.Games.One[i]}, {NewLeaders.Games.Two[i]}
              </p>
              <p>
                <span>العاب تحت :</span>
                {NewLeaders.Games2.One[i]}, {NewLeaders.Games2.Two[i]}
              </p>
              <p>
                <span>قيادة المجلس والصلاة :</span>
                {NewLeaders.Prayers[i]}
              </p>
              <p>
                <span>ترانيم :</span>
                {NewLeaders.Hymns[i]}
              </p>
              <p>
                <span>الايه :</span>
                {NewLeaders.Verses[i]}
              </p>
              <p>
                <span>الدرس :</span>
                {NewLeaders.Lesson[i]}
              </p>
              <p>
                <span>الدرس :</span>
                {formatDate(NewLeaders.Dates[i])}
              </p>
            </div>
          );
        })}
      </div>
    </section>
  );
}
