/* eslint-disable no-undef */
import React, { useEffect, useState } from "react";
import { OpenAI } from "openai";
export default function Home() {
  let [AllMessages, SetAllMessages] = useState([]);
  let [AllMessages2, SetAllMessages2] = useState([]);
  let [IsStart, SetIsStart] = useState(false);
  const Speack = (Text) => {
    const SpeecClass = new SpeechSynthesisUtterance();
    SpeecClass.text = Text;
    SpeecClass.voice = window.speechSynthesis.getVoices()[2];
    window.speechSynthesis.speak(SpeecClass);
  };
  const Listen = () => {
    let SpeechRecognition = new webkitSpeechRecognition();

    SpeechRecognition.onstart = () => {
      SetIsStart(true);
    };
    SpeechRecognition.onend = () => {
      SetIsStart(false);
    };
    SpeechRecognition.onresult = (e) => {
      let Text = e.results[0][0].transcript;
      const RealApiKey = "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";
      let Chat = new OpenAI({
        apiKey: RealApiKey,
        dangerouslyAllowBrowser: true,
      });
      AllMessages2.push({ Who: "You", Body: e.results[0][0].transcript });
      SetAllMessages2(AllMessages2);
      const response = Chat.chat.completions
        .create({
          model: "gpt-3.5-turbo-1106",
          messages: [{ role: "user", content: Text }],
        })
        .then((result) => {
          AllMessages.push({
            Who: "AI",
            Body: result.choices[0].message.content,
          });
          SetMessages3(AllMessages);
          return result.choices[0].message.content;
        });
      response.then((result) => {
        Speack(result);
      });
    };
    SpeechRecognition.start();
  };
  const SetMessages3 = (Messages) => {
    SetAllMessages2(Messages);
  };

  return (
    <section id="HomePage">
      {IsStart ? <h2>Recording....</h2> : ""}
      <button
        className="btn btn-primary"
        onClick={() => {
          Listen();
        }}
      >
        Start Question
      </button>
      {/* <div className="container">
        <div className="row">
          <div className="col-12">
          
          </div>
        </div> */}
      {/* <div className="row">
          <div className="col-12">
            <div className="Messages">
              {AllMessages2.map((Message, i) => {
                return (
                  <div className="Message" key={i}>
                    {Message.Who} : <p>{Message.Body}</p>
                  </div>
                );
              })}
            </div>
          </div>
        </div>  */}
      {/* </div> */}
    </section>
  );
}
